<template>
  <div class="upload-page">
    <div class="topbar">
      <img src="../assets/cancel.png" alt="" />
      <p v-if="foodEdit" @click="uploadFood">publish food</p>
      <p v-else @click="uploadMethod">publish method</p>
      <div id="option"></div>
    </div>
    <food-info-upload v-if="foodEdit" @finishFoodUpload="startMethod" />
    <upload-food-method v-else />
  </div>
</template>

<script>
import FoodInfoUpload from "../components/foodInfoUpload.vue";
import UploadFoodMethod from "../components/uploadFoodMethod.vue";

export default {
  components: { FoodInfoUpload, UploadFoodMethod },
  data() {
    return {
      foodEdit: true,
      food_id: null,
    };
  },
  methods: {
    startMethod(data) {
      this.foodEdit = false;
      this.$store.commit("saveEditFoodId", data);
    },
    uploadMethod() {
      this.$store.commit("uploadMethodData");
    },
    uploadFood() {
      this.$store.commit("uploadFoodData");
    },
  },
};
</script>

<style lang="scss" scoped>
@import "../assets/style/variable.scss";
.upload-page {
  //   background-color: $bgc;
  width: 100vw;
  min-height: 100vh;
  position: relative;
  .topbar {
    position: sticky;
    top: 0;
    width: 100%;
    height: 3em;
    background-color: #fff;
    display: grid;
    grid-template-columns: 1fr 8fr 1fr;
    align-items: center;
    justify-items: center;
    img {
      height: 1em;
    }
    p {
      justify-self: end;
      background-color: $barBgc;
      padding: 0.2em 1em;
      color: #fff;
      border-radius: 1rem;
    }
  }
}
</style>