<template>
  <div class="userPage">
    <div class="top">
      <search-bar />
      <div v-if="ShowBtn" @click="addNew" class="addNew">
        <img src="../assets/add-file.png" alt="" />
      </div>
    </div>
    <div class="user-info">
      <p class="name">{{ user.username }}</p>
      <p>Location: {{ user.location }}</p>
      <p>Birthdate: {{ user.birthday }}</p>
      <p>Join date: {{ user.join_date }}</p>
      <p>Bio: {{ user.bio }}</p>
      <img :src="user.icon" alt="" />
    </div>
    <div class="toggle-button">
      <div @click="shiftProduction" class="production">
        <p>production</p>
        <div v-if="toggle == 'production'" class="line"></div>
      </div>
      <div @click="shiftcollection" class="collection">
        <p>collection</p>
        <div v-if="toggle == 'collection'" class="line"></div>
      </div>
    </div>
    <div v-if="toggle == 'production'" class="user-production card-area">
      <food-card
        class="card"
        v-for="food in productions"
        :key="food.food_id"
        :food="food"
      />
    </div>
    <div v-if="toggle == 'collection'" class="user-collection card-area">
      <food-card
        class="card"
        v-for="food in collectionFoods"
        :key="food.food_id"
        :food="food"
      />
    </div>
    <bottom-bar />
  </div>
</template>

<script>
import axios from "axios";
import cookies from "vue-cookies";
import searchBar from "../components/searchBar.vue";
import BottomBar from "../components/bottomBar.vue";
import FoodCard from "../components/foodCard.vue";
export default {
  components: { searchBar, BottomBar, FoodCard },
  data() {
    return {
      owener: cookies.get("user"),
      user: {},
      ifOwner: false,
      productions: [],
      ShowBtn:false
    };
  },
  props: {
    food: {
      type: Object,
    },
  },
  methods: {
    addNew() {
      this.$router.push("/upload");
    },
    getUsers() {
      axios
        .request({
          url: "https://homedelicious.ml/api/users",
          method: "get",
          params: {
            user_id: this.$router.history.current.params.pathMatch,
          },
        })
        .then((response) => {
          console.log(response.data);
          this.user = response.data;
          if (this.owener.user_id == this.user.user_id) {
            this.ifOwner = true;
          }
          console.log(this.ifOwner);
        })
        .catch((error) => {
          console.log(error);
        });
    },
    getProduction() {
      axios
        .request({
          url: "https://homedelicious.ml/api/foods",
          method: "get",
          params: {
            user_id: this.$router.history.current.params.pathMatch,
          },
        })
        .then((response) => {
          console.log(response.data);
          this.productions = response.data;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    shiftProduction() {
      this.$store.commit("userpageChoice", "production")
    },
    shiftcollection() {
      this.$store.commit("userpageChoice","collection")
    },
    checkOwner(){
      if (this.$store.getters.login){
        if(cookies.get('user').user_id == this.$router.history.current.params.pathMatch){
          this.ShowBtn = true 
        }
      }
    }
  },
  computed: {
    collectionFoods() {
      return this.$store.state.collectionList
    },
    toggle() {
      return this.$store.state.userpageToggle
    },
    SearchContent() {
      return this.$store.state.searchShow;
    },
    searchFoodList() {
      return this.$store.state.searchFoodList;
    },

  },
  mounted() {
    this.$store.state.searchShow = false;
    this.getUsers();
    this.getProduction();
    this.checkOwner()
    if (this.collectionFoods.length == 0){
      axios
        .request({
          url: "https://homedelicious.ml/api/collection",
          method: "get",
          params: {
            user_id: this.$router.history.current.params.pathMatch,
          },
        })
        .then((response) => {
          console.log(response.data);
          this.$store.commit("saveCollectionList",response.data);
        })
        .catch((error) => {
          console.log(error);
        });
    }
  },
};
</script>

<style lang="scss" scoped>
@import "../assets/style/variable.scss";
.userPage {
  font-family: $fonts;
  width: 100vw;
  height: 100vh;
  background-color: $bgc;
  position: relative;
  display: grid;
  grid-template-rows: 10% 24% 5% 53%;
  overflow: hidden;
  .top {
    box-sizing: border-box;
    position: relative;
    display: grid;
    grid-template-columns: 4fr 1fr;
    justify-items: center;
    align-items: center;
    .search-bar {
      width: 100%;
    }
    .addNew {
      justify-self: start;
      width: 12vw;
      height: 12vw;
      background-color: $barBgc;
      border-radius: 50%;
      display: grid;
      justify-items: center;
      align-items: center;
      filter: $shadow;
      img {
        width: 6vw;
      }
    }
  }
  .user-info {
    position: relative;
    color: $fontColordark;
    box-sizing: border-box;
    padding: 3em;
    display: grid;
    row-gap: 0.3em;
    font-size: 0.8rem;
    .name {
      font-size: 2rem;
      font-weight: bold;
    }
    img {
      width: 25vw;
      height: 25vw;
      border-radius: 50%;
      filter: $shadow;
      object-fit: cover;
      position: absolute;
      right: 3em;
      top: 2em;
    }
  }
  .toggle-button {
    width: 60%;
    margin-left: 20%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    p {
      text-transform: uppercase;
      filter: $shadow;
      font-weight: 500;
    }
    .line {
      margin-top: 5px;
      border-radius: 2px;
      height: 5px;
      width: 80%;
      background-color: $redBgc;
    }
  }
  .bottom-bar {
    z-index: 50;
    width: 100%;
    height: 8vh;
    background-color: #fff;
    position: fixed;
    bottom: 0;
  }
  .card-area {
    margin-top: 2vh;
    display: grid;
    row-gap: 5vh;
    overflow-y: scroll;
    .card {
      margin-left: 10%;
    }
  }
}
</style>