<template>
  <div class="method-div">
    <div class="ingredient-area">
      <h2>Ingredients:</h2>
      <div class="ingredient-preview">
        <prematertial-div
          v-for="(material, index) in preview_ingredients"
          :key="index"
          :prematerial="material"
          :index="index"
          @updateIngredient="updateIngredient"
          @deleteIngredient="deleteIngredient"
        />
      </div>
      <div class="ingredient-edit">
        <div class="data">
          <div class="round"></div>
          <input
            type="text"
            name=""
            id="name"
            placeholder="Butter"
            v-model="material"
          />
        </div>
        <div class="data">
          <div class="round"></div>
          <input
            type="text"
            name=""
            id="amount"
            placeholder="150g"
            v-model="amount"
          />
        </div>
        <div class="button">
          <button class="add" @click="addIngredient">Add</button>
          <button class="confirm">confirm</button>
        </div>
      </div>
    </div>
    <div class="method-area">
      <h2>Methods:</h2>
      <pre-method-div
        v-for="(method, index) in pre_methods"
        :key="index"
        :preMethod="method"
        :index="index"
        @deleteStep="deleteStep"
        @updateMethod="updateMethod"
      />
      <div class="method-edit">
        <h3>STEP: {{ method_step }}</h3>
        <textarea
          name="Text1"
          cols="40"
          rows="5"
          id="method"
          placeholder="Tell us how you make the food...."
          v-model="method_text"
        ></textarea>
        <img v-if="method_image != ''" :src="method_image" alt="" />
        <p v-else class="img-title">
          Please upload your method photo
          <image-upload @newImage="newImage" />
        </p>
        <div class="button">
          <button class="add" @click="addMethod">Add</button>
          <button class="confirm">confirm</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ImageUpload from "./imageUpload.vue";
import PrematertialDiv from "./prematertialDiv.vue";
import PreMethodDiv from "./preMethodDiv.vue";
import axios from "axios";

export default {
  components: { PrematertialDiv, ImageUpload, PreMethodDiv },
  data() {
    return {
      ifpreview: false,
      material: "",
      amount: "",
      ingredient: "",
      preview_ingredients: [],
      method_image: "",
      method_text: "",
      pre_methods: [],
      methods: "",
    };
  },
  props: {
    prematerial: {
      type: Array,
    },
    preMethod: {
      type: Array,
    },
    index: {
      type: Number,
    },
  },
  methods: {
    addIngredient() {
      let ingredient = [this.material, this.amount];
      this.preview_ingredients.push(ingredient);
      this.material = "";
      this.amount = "";
      console.log(this.preview_ingredients);
    },
    updateIngredient(data1, data2) {
      console.log(data1);
      console.log(data2);
      this.preview_ingredients.splice(data2, 1, data1);
      console.log(this.preview_ingredients);
    },
    updateMethod(data1, data2) {
      console.log(data1);
      console.log(data2);
      this.pre_methods.splice(data2, 1, data1);
      console.log(this.pre_methods);
    },
    deleteIngredient(data) {
      this.preview_ingredients.splice(data, 1);
      console.log(this.preview_ingredients);
    },
    deleteStep(data) {
      this.pre_methods.splice(data, 1);
      console.log(this.pre_methods);
    },
    newImage(data) {
      this.method_image = data;
    },
    addMethod() {
      let method = [this.method_text, this.method_image];
      this.pre_methods.push(method);
      this.method_text = "";
      this.method_image = "";
      console.log(this.pre_methods);
    },
  },
  watch: {
    ifupload(newValue) {
      console.log(newValue);
      if (newValue == true) {
        for (let i = 0; i < this.preview_ingredients.length; i++) {
          this.ingredient =
            this.ingredient +
            this.preview_ingredients[i][0] +
            "<###**%%###>" +
            this.preview_ingredients[i][1] +
            "<###**^^###>";
        }
        if (this.material != "" || this.amount != "") {
          this.ingredient =
            this.ingredient +
            this.material +
            "<###**%%###>" +
            this.amount +
            "<###**^^###>";
        }
        console.log(this.ingredient);
        for (let i = 0; i < this.pre_methods.length; i++) {
          this.methods =
            this.methods +
            this.pre_methods[i][0] +
            "<###**%%###>" +
            this.pre_methods[i][1] +
            "<###**^^###>";
        }
        if (this.method_text != "" || this.method_image != "") {
          console.log("aa");
          this.methods =
            this.methods +
            this.method_text +
            "<###**%%###>" +
            this.method_image +
            "<###**^^###>";
        }
        axios
          .request({
            url: "https://homedelicious.ml/api/methods",
            method: "post",
            data: {
              token: "JiP6w1TdsDf7MVN2ltsTN0yGpSr2fWG4K6h6AP9rIgelT0Kavb",
              food_id: this.$store.state.edit_food_id,
              ingredient: this.ingredient,
              process: this.methods,
            },
          })
          .then((response) => {
            console.log(response.data);
            this.$router.push('/food/' + this.$store.state.edit_food_id)
          })
          .catch((error) => {
            console.log(error);
          });
      }
    },
  },

  computed: {
    method_step() {
      return this.pre_methods.length + 1;
    },
    ifupload() {
      return this.$store.state.ifuploadMethodData;
    },
  },
};
</script>

<style lang="scss" scoped>
@import "../assets/style/variable.scss";
.method-div {
  box-sizing: border-box;
  padding: 8vw;
  width: 100%;
  min-height: 100vh;
  font-family: $fonts;
  background-color: $bgc;
  .round {
    width: 2vw;
    height: 2vw;
    border-radius: 50%;
    background-color: $redBgc;
  }
  h2 {
    margin: 2vh 0;
  }
  .button {
    padding: 3vh;
    display: flex;
    justify-content: space-evenly;
    button {
      background-color: $fontColordark;
      color: #fff;
      border-radius: 0.5rem;
      padding: 5px;
      text-transform: uppercase;
      font-family: $fonts;
      border: 1px solid $fontColordark;
    }
  }
  .ingredient-area {
    .ingredient-edit {
      .data {
        margin-bottom: 2vh;
        display: grid;
        grid-template-columns: 1fr 8fr;
        align-items: center;
        input {
          background-color: #fff;
          font-size: 1.2rem;
          line-height: 2rem;
          margin-right: 5vw;
        }
      }
    }
    .ingredient-preview {
      display: grid;
      row-gap: 2vh;
      margin: 4vh 0;
    }
  }
  .method-area {
    .method-edit {
      margin-top: 2vh;
      display: grid;
      row-gap: 2vh;
      justify-items: center;
      textarea {
        width: 90%;
        background-color: #fff;
        font-size: 1.2rem;
        line-height: 2rem;
        margin-right: 5vw;
      }
      img {
        justify-self: start;
        width: 90%;
        height: 60vw;
        object-fit: cover;
      }
    }
  }
}
</style>