<template>
  <div class="prematerial-div">
    <div class="round"></div>
    <input
      v-if="edit"
      class="material"
      type="text"
      name=""
      v-model="new_material"
    />
    <div v-else class="material">{{ material }}</div>
    <input
      v-if="edit"
      class="amount"
      type="text"
      name=""
      v-model="new_amount"
    />
    <div v-else class="amount">{{ amount }}</div>
    <img v-if="edit" src="../assets/confirm.png" alt="" @click="Confirm" />
    <img v-else src="../assets/edit.png" alt="" @click="Edit" />
    <img src="../assets/delete.png" alt="" @click="Delete" />
  </div>
</template>

<script>
export default {
  data() {
    return {
      edit: false,
      new_material: null,
      new_amount: null,
    };
  },
  props: {
    prematerial: {
      type: Array,
    },
    index: {
      type: Number,
    },
  },
  methods: {
    Edit() {
      this.new_material = this.material;
      this.new_amount = this.amount;
      this.edit = true;
    },
    Confirm() {
      let new_ingredient = [this.new_material, this.new_amount];
      console.log(new_ingredient);
      this.$emit("updateIngredient", new_ingredient, this.index);
      this.edit = false;
    },
    Delete() {
      console.log(this.index);
      this.$emit("deleteIngredient", this.index);
    },
  },
  computed: {
    material() {
      return this.prematerial[0];
    },
    amount() {
      return this.prematerial[1];
    },
  },
};
</script>

<style lang="scss" scoped>
@import "../assets/style/variable.scss";

.prematerial-div {
  display: grid;
  grid-template-columns: 1fr 4fr 4fr 1fr 1fr;
  font-size: 1.2rem;
  font-family: $fonts;
  align-items: center;

  .round {
    width: 2vw;
    height: 2vw;
    border-radius: 50%;
    background-color: $redBgc;
  }
  .material,
  .amount {
    width: 100%;
  }
  input {
    border: 1px solid $fontColorlight;
  }
  img {
    height: 1.5rem;
    background-color: $fontColorlight;
    padding: 0.2em;
    border-radius: 0.2em;
    align-self: end;
  }
}
</style>