<template>
  <div class="preMethod-div" >
    <h3 class="number">STEP: {{ index + 1 }}</h3>
    <div class="option">
      <img class="option-icon" src="../assets/option.png" alt="" @click="Option"/>
      <div v-if="option" class="button">
        <button @click="deleteStep">Delete Step</button>
        <button @click="deleteImage">Delete image</button>
      </div>
    </div>
    <textarea
      name="Text1"
      cols="40"
      rows="5"
      id="method"
      placeholder="Tell us how you make the food...."
      v-model="new_method_text"
    ></textarea>
    <img v-if="new_method_image != ''" :src="new_method_image" alt="" />
    <p v-else class="img-title">
      Please upload your method photo
      <image-upload @newImage="newImage" />
    </p>
  </div>
</template>

<script>
import imageUpload from "./imageUpload.vue";
export default {
  components: { imageUpload },
  data() {
    return {
      new_method_text: "",
      new_method_image: "",
      option: false,
    };
  },
  props: {
    preMethod: {
      type: Array,
    },
    index: {
      type: Number,
    },
  },
  methods: {
      Option() {
          this.option= !this.option
      },
        deleteStep(){
           this.$emit("deleteStep", this.index); 
           this.option = false
        },
        deleteImage(){
            this.new_method_image=""
           this.option = false
        },
        newImage(data){
            this.new_method_image = data
        },
        // updata(){
        //     console.log(this.new_method_text)
        //     console.log(this.new_method_image)
        // }

  },
watch: {
    new_method_text(newValue) {
        console.log(newValue)
        let data = [newValue, this.new_method_image]
        console.log(data)
        this.$emit("updateMethod", data,this.index)
    },
    new_method_image(newValue) {
        console.log(data)
        let data = [this.new_method_text, newValue]
        this.$emit("updateMethod", data,this.index)
    }
},
  computed: {
    method_text() {
      return this.preMethod[0];
    },
    image() {
      return this.preMethod[1];
    },
  },
  mounted () {
      this.new_method_text = this.method_text;
      this.new_method_image = this.image;
  },
};
</script>

<style lang="scss" scoped>
@import "../assets/style/variable.scss";

.preMethod-div {
  display: grid;
  row-gap: 2vh;
  position: relative;
  h3 {
    width: 90%;
    text-align: center;
  }
  .option {
      position: absolute;
      right: 8vw;
    .option-icon {
      height: 1rem;
      width: auto;
    }
    .button{
        position: absolute;
        right: 5vw;
        width:20vw;
        display: grid;
        background-color: $fontColordark;
        color: #fff;
        filter: $shadow;
        button{
            padding: 5px;
            border-bottom: 1px solid #fff;
            &:hover{
                color:$fontColordark;
                background-color:#fff
                
            }
        }
    }
  }
  textarea {
    width: 90%;
    background-color: #fff;
    font-size: 1.2rem;
    line-height: 2rem;
    margin-right: 5vw;
  }
  img {
    justify-self: start;
    width: 90%;
    height: 60vw;
    object-fit: cover;
  }
}
</style>