<template>
  <div class="process-div">
      <div class="number">STEP: {{index}}</div>
      <div class="single-process">{{process}}</div>
      <img class="image" :src="image">
  </div>
</template>

<script>
export default {
    data() {
        return {
            process: "",
            image:""
        }
    },
  props: {
    step: {
      type: String,
    },
    index: {
      type: Number,
    },
  },
    mounted () {
    this.process = this.step.split("<###**%%###>")[0];
    this.image = this.step.split("<###**%%###>")[1];
  },
};
</script>

<style lang="scss" scoped>
.process-div{
    display: grid;
    justify-items: center;
    row-gap: 2vh;
    row
    .number{
        font-size: 1.2rem;
        
    }
    .single-process{
        width: 90%;
    }
    img{
        width: 80%;
    }
}
</style>