<template>
  <div class="ingredient">
      <h2>Ingredients:</h2>
      <div class="area">
          <material-div v-for="material in ingredientList" :key="material" :material="material"/>
      </div>
  </div>
</template>

<script>
import materialDiv from './materialDiv.vue';
export default {
  components: { materialDiv },
    data() {
        return {
        }
    },
  props: {

    material:{
        type:String
    }
  },
  computed: {
    ingredientList() {
      if (this.$store.state.method.ingredient == undefined) return [];
      let list = this.$store.state.method.ingredient.split("<###**^^###>")
      list.pop()
      return list
    }
  },

};
</script>

<style lang="scss" scoped>
@import "../assets/style/variable.scss";
.ingredient{
    margin-top: 5vh;
    width: 100%;
    font-family: $fonts;
    .area{
      margin-top: 5vh;
        display: grid;
        row-gap: 2vh;
    }
}
</style>